.bodyStyles {
	background: var(--bg-light);
}
.addCheckImagesTitle {
	padding: 14px 0 18px 0;
}

.input_picker {
	border-radius: 4px;
	font-size: 14px;
	width: 100%;
	height: 48px;
	padding-left: 16px;
}

.upload {
	border-radius: 4px;
	background-color: #fff;
	padding: 16px 24px;
	margin-bottom: 16px;
}

.listRequirements {
	font-size: 14px;
	padding: 16px 0;
	padding-left: 4px;
	list-style: none;
}

.reupload {
	cursor: pointer;
	font-size: 14px;
	margin-left: auto;
	text-decoration: underline;
}

.requireInfo {
	display: list-item;
	list-style-type: disc;
	text-align: left;
}